    </div>
    <div class="cube" style="--z: 5" text-data=""><a>服装饰品</a></div>
    <div class="cube" style="--z: 4" text-data=""><a>电影娱乐</a></div>
    <div class="cube" style="--z: 3" text-data=""><a>数码中心</a></div>
    <div class="cube" style="--z: 2" text-data=""><a>美食天地</a></div>
    <div class="cube" style="--z: 1" text-data="">
      <router-link to="/">楼层特效</router-link>
    </div>
  </div>
  <router-view></router-view>
</template>

<script>
export default {
  name: "floor3D",
};
</script>

<style>
/* 实现原理
    1.利用skewY实现角度倾斜  transform: skewY(-10deg);
    2.利用色环角度调整颜色  calc()支持里面写表达式
    3.利用伪类对cube添加面，通过skewY倾斜，实现立方体效果
    4.利用hover 实现移动效果
*/
/* 整栋楼在页面中位置 */
.main {
  position: absolute;
  width: 300px;
  height: 800px;
  display: flex;
  right: 0px;
  justify-content: center;
  align-items: end;
  flex-direction: column;
}
.cube {
  width: 200px;
  height: 75px;
  right: 0;
  position: relative;
  background: #666;
  transition: 0.2s linear;
  z-index: var(--z);
}
.cube a {
  font-family: Arial, Helvetica, sans-serif;
  position: absolute;
  width: 100%;
  height: 100%;
  font-size: 30px;
  text-decoration: none;
  line-height: 75px;
  text-align: center;
  color: hsl(calc(var(--z) * 60 * 1deg), 50%, 50%);
  text-shadow: 1px 2px rgb(21, 20, 20);
}
.cube::before {
  font-family: FontAwesome;
  /* attr() 函数返回选择元素的属性值。 */
  content: attr(text-data);
  text-align: center;
  line-height: 75px;
  font-size: 45px;
  width: 50px;
  height: 75px;
  position: absolute;
  background: #666;
  left: -50px;
  filter: brightness(0.7);
  transform-origin: 100% 0;
  transform: skewY(30deg);
  transition: 0.2s linear;
  color: hsl(calc(var(--z) * 60 * 1deg), 50%, 50%);
}
.cube::after {
  content: "";
  width: 200px;
  height: 30px;
  position: absolute;
  background: #666;
  top: -28px;
  filter: brightness(0.6);
  transform-origin: 0 100%;
  transform: skewX(60deg);
}
.cube:hover {
  right: 100px;
  background: hsl(calc(var(--z) * 60 * 1deg), 50%, 50%);
}
.cube:hover::before,
.cube:hover::after {
  background: hsl(calc(var(--z) * 60 * 1deg), 50%, 50%);
}
.cube a:hover {
  color: white;
}
</style>